






























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:"",
			swipeList: ['../../static/index/banner.jpg','../../static/index/banner.jpg'],
			indicatorDots: true,
			indicatorcolor: 'white',
			indicatoractivecolor:'#FEC200',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			newlist: [],
			recomlist: [],
			classlist:[],
			provinceName:'',
			countyAreaName:'',
			cityName:''
		}
	},
	onLoad() {
		this.static=ut.static;
	},
	onShow() {
		if(!this.newlist.length){
			this.req_new();
		}
		if(!this.recomlist.length){
			this.req_recom();
		}
		if(!this.classlist.length){
			this.req_class();
		}
	},
	methods: {
		bindRegionChange: function (e) {
			this.provinceName=e.target.value[0];
			this.countyAreaName=e.target.value[1];
			this.cityName=e.target.value[2];
		},
		call(){
			ut.call();
		},
		go_build_build2(_id){
			wx.navigateTo({
				url: `../build/build2?_id=${_id}`
			})
		},
		go_build_serch(){
			wx.navigateTo({
				url: '../build/serch'
			})
		},
		go_build_mallinf(_id,name){
			wx.navigateTo({
				url: `../build/mallinf?_id=${_id}&&title=${name}`
			})
		},
		req_class(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/class"
			}).then(data=>{
				console.log(data)
				this.classlist=data;
			})
		},
		req_new(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/newlist"
			}).then(data=>{
				this.newlist=data
			})
		},
		req_recom(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/recommendlist"
			}).then(data=>{
				this.recomlist=data
			})
		}
	}
}
